import app from './_firebase';

import {getFirestore} from 'firebase/firestore'
import {getStorage} from 'firebase/storage'
import Axios from 'axios';
import validUrl  from 'valid-url';

export const listLiveEvent = async () => {
  try {
   let ListLiveEvent = [{
      id:0,
      title: "Chọn live Event"
    }];
    const start = new Date();
    let querySnapshot = await app.firestore().collection("live_events").where('start_at','>=', start).get();
    querySnapshot.docs.map((doc) => {
      ListLiveEvent.push({
        id: doc.id,
        ...doc.data()
      });
    });
    return ListLiveEvent;
  } catch (err) {
    console.log(err);
  }
};

export const listPopup = async () => {
  try {
    let result = await  Axios({
      method: 'GET',
      url: process.env.REACT_APP_SERVER_URL+'/api/getpopup',
      headers:{
        'Content-Type': 'application/json',
        'Access-Control-Allow-Origin' : '*',
        'Access-Control-Allow-Methods':'GET,PUT,POST,DELETE,PATCH,OPTIONS',
      }
    });
    let popups = result.data;
    let resPop = [{
      id:1,
      title:'Chọn PopUp'
    }];
    for (let property in popups) {
      resPop.push(popups[property]);
    }
    console.log(resPop);
    return resPop;
  }catch (err){
  console.log(err);
    let resPop = [{
      id:1,
      title:'Chọn PopUp'
    }];
  return resPop;
}
};

export const autoSubmit = async (data, linkBitly) => {
  try {
    const db = app.firestore();
    let option = '';
    if(data.policy === true){
      option = 'keep_time'
    }
    if(typeof linkBitly !=="undefined"){
      console.log(data.liveOption);
      let liveContentRef = db.collection("live_events").doc(data.liveOption).collection("detail").doc("content");
      let doc = await liveContentRef.get();
        if (doc.exists) {
          // console.log("Document data:", doc.data());
          const  old_content = doc.data().data;
          const new_content  = old_content.replace('a href="https://www.facebook.com/Hocbaionha/live"', 'a href="'+linkBitly+'"');
          await db.collection("live_events").doc(data.liveOption).collection("detail").doc("content").set({"data": new_content});
          await sendNotify(data.liveOption, option);
          return {
            error_code:0,
            message:"Gửi tin nhắn thành công."
          };
        } else {
          // doc.data() will be undefined in this case
          return {
            error_code:1,
            message:"Id live_event không tồn tại."
          }
        }
    }else{
      return {
        error_code:1,
        message:"Link Bitly không tồn tại"
      }
    }
  }catch (err){
    console.log(err);
  }
};

export const getLinkBitly = async(linkLive)=>{
  try {
    let result = await  Axios({
      method: 'GET',
      url: process.env.REACT_APP_SERVER_URL+'/api/getLinkBitly?link_live='+linkLive,
      headers:{
        'Content-Type': 'application/json',
        'Access-Control-Allow-Origin' : '*',
        'Access-Control-Allow-Methods':'GET,PUT,POST,DELETE,PATCH,OPTIONS',
      }
    });

    return  result.data;
  }catch (err){
    console.log(err);
  }
};

async function sendNotify(LiveEventId, option){
  try {
    let result = await  Axios({
      method: 'POST',
      url: process.env.REACT_APP_SERVER_URL+'/api/sendnoti',
      headers:{
        'Content-Type': 'application/json',
        'Access-Control-Allow-Origin' : '*',
        'Access-Control-Allow-Methods':'GET,PUT,POST,DELETE,PATCH,OPTIONS',
        'Authorization': 'Bearer f56e78db35ab4ff8c915facfa644c4ad25b6358b',
      },
      data: {
        live_event_id:LiveEventId,
        option:option
      }
    });
    return  result.data;
  }catch (err){
    console.log(err);
  }
}



