import React, { useCallback, useEffect, useState } from 'react';
import * as Yup from 'yup';
import { Formik } from 'formik';
import {
  Box,
  Button,
  Card,
  CardContent,
  CardHeader,
  Checkbox,
  CircularProgress,
  Divider,
  FormHelperText,
  Grid,
  Link,
  TextField,
  Typography
} from '@material-ui/core';
import { Alert } from '@material-ui/lab';
import wait from 'src/utils/wait';
import { makeStyles } from '@material-ui/core/styles';
import {listLiveEvent, listPopup, autoSubmit, getLinkBitly} from './../../../../api/auto'
import useIsMountedRef from '../../../../hooks/useIsMountedRef';

console.log(listPopup);
const useStyles = makeStyles((theme) => ({
  brief: {
    '& .ql-editor':{
      height:150,
    }
  },
  tags: {
    position: 'relative',
    overflow: 'auto',
    maxHeight: 300,
  } ,
  categories: {
    position: 'relative',
    overflow: 'auto',
    maxHeight: 300,
  } ,
  pre: {
    backgroundColor: "#fff"
  },
  toolbar: {
    marginBottom: 0,
    borderLeft: 'none',
    borderTop: 'none',
    borderRight: 'none',
    borderBottom: `1px solid ${theme.palette.divider}`,
    background: 'transparent'
  },
  editor: {
    padding: theme.spacing(2),
    color: theme.palette.text.primary
  }
}));

const AutoCreateForm = () => {
  const classes = useStyles();
  const isMountedRef = useIsMountedRef();
  const [isGetLinkSuccess, setStatusGetLink] = useState();
  const [isAutoSubmit, setStatusAutoSubmit] = useState();

  const [liveOptions, setLiveOptions] = useState([]);

  const getLiveOptions = useCallback(async () => {
    try {
      const liveOptions = await listLiveEvent();
      console.log(liveOptions);
      if (isMountedRef.current) {
        setLiveOptions(liveOptions);
      }
    } catch (err) {
      console.error(err);
    }
  }, [isMountedRef]);

  useEffect(() => {
    getLiveOptions();
  }, [getLiveOptions]);

  const [popupOptions, setPopupOptions] = useState([]);


   const getPopupOptions = useCallback(async () => {
    try {
      const popupOptions = await listPopup();
      console.log(popupOptions);
      if (isMountedRef.current) {
        setPopupOptions(popupOptions);
      }
    } catch (err) {
      console.error(err);
    }
  }, [isMountedRef]);

  useEffect(() => {
    getPopupOptions();
  }, [getPopupOptions]);

  return (
    <Formik
      initialValues={{
        liveOption:'',
        linkLive: '',
        popupOption: '',
        policy: false,
        submit: null
      }}
      validationSchema={Yup.object().shape({
        linkLive: Yup.string().required('Required'),
        liveOption: Yup.string().required('Required'),
      })}
      onSubmit={async (values, {
        resetForm,
        setErrors,
        setStatus,
        setSubmitting
      }) => {
        try {
          // NOTE: Make API request
          await wait(1000);
          // resetForm();
          let LinkBitLy = await getLinkBitly(values.linkLive);
          if(LinkBitLy.link){
            setStatusGetLink(true);
            let result = await autoSubmit(values,  LinkBitLy.link);
            console.log(result);
            if(result.error_code ===0){
              setStatusAutoSubmit(true);
            }else{
              setStatusAutoSubmit(false);
            }

          }else{
            setStatusGetLink(false);
            values.linkBitly = 'undefined';
          }
          // let result = await autoSubmit(values);
          console.log(LinkBitLy);
          setStatus({ success: true });
          setSubmitting(false);
        } catch (err) {
          console.error(err);
          setStatus({ success: false });
          setErrors({ submit: err.message });
          setSubmitting(false);
        }
      }}
    >
      {({
          errors,
          handleBlur,
          handleChange,
          handleSubmit,
          isSubmitting,
          touched,
          values
        }) => (
        <Card>
          <CardHeader title="Basic Form" />
          <Divider />
          <CardContent>
            {isGetLinkSuccess===true && (
              <Box mb={3}>
                <Alert
                  onClose={() => setStatusGetLink()}
                  severity="success"
                >
                  Lấy link thành công!
                </Alert>
              </Box>
            )}
            {isGetLinkSuccess===false && (
              <Box mb={3}>
                <Alert
                  onClose={() => setStatusGetLink()}
                  severity="error"
                >
                  Lấy link thất bại!
                </Alert>
              </Box>
            )}

            {isAutoSubmit===true && (
              <Box mb={3}>
                <Alert
                  onClose={() => setStatusAutoSubmit(false)}
                  severity="success"
                >
                  Gửi tin nhắn thành công.
                </Alert>
              </Box>
            )}

            {isAutoSubmit===false && (
              <Box mb={3}>
                <Alert
                  onClose={() => setStatusAutoSubmit(false)}
                  severity="error"
                >
                  Gửi tin nhắn thất bại.
                </Alert>
              </Box>
            )}

            {isSubmitting ? (
              <Box
                display="flex"
                justifyContent="center"
                my={5}
              >
                <CircularProgress />
              </Box>
            ) : (
              <form onSubmit={handleSubmit}>
                <Grid
                  container
                  spacing={2}
                >
                  <Grid
                    item
                    md={12}
                    xs={12}
                  >
                    <TextField
                      error={Boolean(touched.linkLive && errors.linkLive)}
                      fullWidth
                      helperText={touched.linkLive && errors.linkLive}
                      label="Link Live"
                      name="linkLive"
                      onBlur={handleBlur}
                      onChange={handleChange}
                      value={values.linkLive}
                      variant="outlined"
                    />
                  </Grid>
                  <Grid
                    item
                    md={12}
                    xs={12}
                  >
                    { liveOptions.length > 0 && (<TextField className={classes.liveOptions}
                      error={Boolean(touched.liveOption && errors.liveOption)}
                      label="Live Event"
                      name="liveOption"
                      onChange={handleChange}
                      select
                      SelectProps={{ native: true }}
                      value={values.liveOption}
                      variant="outlined"
                      fullWidth
                    >
                      {liveOptions.map((option) => (
                        <option
                          key={option.id}
                          value={option.id}
                        >
                          {option.title}
                        </option>
                      ))}
                    </TextField>
                      )}
                  </Grid>
                  <Grid
                    item
                    md={12}
                    xs={12}
                  >
                    { popupOptions.length > 0 && (<TextField className={classes.popupOptions}
                      label="Popup Moodle"
                      name="popupOption"
                      onChange={handleChange}
                      select
                      SelectProps={{ native: true }}
                      value={values.popupOption}
                      variant="outlined"
                      fullWidth
                    >
                      {popupOptions.map((option) => (
                        <option
                          key={option.id}
                          value={option.id}
                        >
                          {option.title}
                        </option>
                      ))}
                    </TextField>
                    )}
                  </Grid>


                </Grid>

                <Box
                  alignItems="center"
                  display="flex"
                  mt={2}
                  ml={-1}
                >
                  <Checkbox
                    checked={values.policy}
                    name="policy"
                    onChange={handleChange}
                  />
                  <Typography
                    variant="body2"
                    color="textSecondary"
                  >
                    Run auto  keep Start_time  Event
                  </Typography>
                </Box>
                {Boolean(touched.policy && errors.policy) && (
                  <FormHelperText error>
                    {errors.policy}
                  </FormHelperText>
                )}
                {errors.liveOption && <div>{errors.liveOption}</div>}
                <Box mt={2}>
                  <Button
                    color="secondary"
                    disabled={isSubmitting}
                    fullWidth
                    size="large"
                    type="submit"
                    variant="contained"
                  >
                    Run Auto
                  </Button>
                </Box>
              </form>
            )}
          </CardContent>
        </Card>
      )}

    </Formik>
  );
};

export default AutoCreateForm;
